* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
}

.js-list {
  flex-basis: 2000px;
  gap: 30px;
  margin: 0 auto;
  padding: 30px;
  list-style: none;
  display: grid;
}

.item {
  text-align: center;
  background: #fff;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  padding: 22px;
  display: flex;
  box-shadow: 0 6px 18px #0000001f;
}

.item:hover {
  transition: transform .3s, box-shadow .3s;
  transform: translateY(-6px);
  box-shadow: 0 14px 32px #0003;
}

.item-span {
  color: #555;
  background: #eee;
  border-radius: 10px;
  margin-bottom: 14px;
  padding: 6px 10px;
  font-size: 15px;
}

.image {
  object-fit: cover;
  border-radius: 18px;
  width: 100%;
  height: 260px;
  margin-bottom: 18px;
}

.image .title {
  color: #333;
  margin-bottom: 14px;
  font-size: 24px;
  font-weight: 700;
}

.image .calories, .image .description, .image .price {
  color: #444;
  margin-bottom: 10px;
  font-size: 18px;
}

.image .price {
  color: #e67e22;
  margin-top: 12px;
  font-size: 22px;
  font-weight: 700;
}

.image .item button {
  cursor: pointer;
  border: none;
  border-radius: 9px;
  margin-top: 10px;
  padding: 8px 15px;
  font-size: 16px;
  transition: background-color .2s;
}

.image [data-action="Delete"] {
  color: #fff;
  background-color: #e74c3c;
}

.image [data-action="Delete"]:hover {
  background-color: #c0392b;
}

.image [data-action="Edit"] {
  color: #fff;
  background-color: #2ecc71;
  margin-left: 10px;
}

.image [data-action="Edit"]:hover {
  background-color: #27ae60;
}

.image .open_modal {
  color: #fff;
  cursor: pointer;
  background-color: #6495ed;
  border: none;
  border-radius: 9px;
  margin: 10px auto;
  padding: 8px 15px;
  font-size: 20px;
  display: block;
}

.image .bakdrop {
  opacity: 0;
  pointer-events: none;
  z-index: 1000;
  background-color: #00000069;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.image .modal {
  background-color: #fff;
  border-radius: 20px;
  width: 550px;
  padding: 25px;
}

.image .modal_label {
  font-size: 20px;
  display: block;
}

.image .modal_form {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.image .modal_inp {
  border: none;
  border-radius: 12px;
  outline: 1px solid gray;
  width: 100%;
  height: 40px;
  margin-top: 5px;
  padding: 4px 20px;
  display: block;
}

.image .modal_submit {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #6495ed;
  border: none;
  border-radius: 12px;
  width: 200px;
  height: 60px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  font-size: 19px;
  display: block;
}
